import axios from "axios";
import React, { useEffect, useState } from "react";
import Slider from "react-slick";
import { Link, useParams } from "react-router-dom";
import Swal from "sweetalert2";

import "./ProductDetails.css";

export default function ProductDetails({ setisLoading }) {
  const [ProductDetails, setProductDetails] = useState([]);
  const [ProductImages, setProductImages] = useState([]);

  let params = useParams();
  async function getProductDetails() {
    setisLoading(true);
    let { data } = await axios.get(
      `https://route-ecommerce.onrender.com/api/v1/products/${params.id}`
    );
    setProductDetails(data.data);
    setProductImages(data.data.images);
    setisLoading(false);
    // console.log(ProductDetails);
  }
  useEffect(() => {
    getProductDetails();
    // console.log(ProductDetails);
  }, []);
  const settings = {
    dots: false,
    fade: true,
    autoplay: true,
    speed: 600,
    autoplaySpeed: 4000,
    cssEase: "linear",
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  const handleAddToCart = (product) => {
    const cartItems = JSON.parse(localStorage.getItem("cartItems")) || [];

    const isAddedToCart = JSON.parse(localStorage.getItem("cartItems")).filter(
      (element) => {
        if (element.id === product.id) {
          return true;
        }

        return false;
      }
    );

    if (isAddedToCart.length > 0) {
      // 👉️ object is contained in the array
      const Toast = Swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });
      Toast.fire({
        icon: "info",
        title: "Already Added to Cart",
      });
    } else {
      cartItems.push(product);
      localStorage.setItem("cartItems", JSON.stringify(cartItems));
      const Toast = Swal.mixin({
        toast: true,
        position: "top-end",
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener("mouseenter", Swal.stopTimer);
          toast.addEventListener("mouseleave", Swal.resumeTimer);
        },
      });
      Toast.fire({
        icon: "success",
        title: "Added to Cart",
      });
    }
  };

  return (
    <div className="container">
      <div className="row align-items-center mt-4">
        <div className="col-md-4">
          <div className="productDetails rounded-3 overflow-hidden cursor-pointer">
            <Slider className="col-md-12" {...settings}>
              {ProductImages.map((img) => (
                <div>
                  <img className="w-100 h-100" src={img} alt="" />
                </div>
              ))}
            </Slider>
          </div>
        </div>
        <div className="col-md-8">
          <h4 className="fw-bold">{ProductDetails.title}</h4>
          <div className="mb-2">
            {" "}
            <span>Rating: </span>
            <i className="fa-solid fa-star"></i>
            <span className="font-sm fw-bold text-dark">
              {ProductDetails.ratingsAverage}
            </span>
          </div>
          <p>
            Price:
            <sup> EGP </sup>
            <span className="text-main fw-bold">
              {ProductDetails.price}
              <sup>.00</sup>
            </span>
          </p>
          <p>{ProductDetails.description}</p>
          <div className="buttons d-flex flex-column col-sm-4 mt-5">
            <Link
              // to={"/cart"}
              onClick={() => handleAddToCart(ProductDetails)}
              className="btn  btn-success border-0 mb-2 "
            >
              Add to Cart
            </Link>
            <Link to={"/cart"} className="btn btn-success border-0">
              View Cart
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}
