import React from "react";
import "./Profile.css";

export default function Profile() {
  console.log(JSON.parse(localStorage.getItem("userProfile")));
  return (
    <>
      <div className="p-5 mt-5 shadow container rounded-3">
        <h1 className="fw-bold mb-4">Profile</h1>
        <p className="fw-bold">
          Name:{" "}
          <span className="text-main fw-normal">
            {JSON.parse(localStorage.getItem("userProfile")).name}
          </span>
        </p>
        <p className="fw-bold">
          ID:{" "}
          <span className="text-main fw-normal">
            {JSON.parse(localStorage.getItem("userProfile")).id}
          </span>
        </p>
        <p className="fw-bold">
          Role:{" "}
          <span className="text-main fw-normal">
            {JSON.parse(localStorage.getItem("userProfile")).role}
          </span>
        </p>
      </div>
    </>
  );
}
